.outContainer {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
}

.modalLogin {
    width: 20rem;
    height: 25rem;
    background-color: black;
    position: absolute;
    left: 50%;
    top: 35%;
    transform: translate(-50%, -50%);
    z-index: 100;
    border: 1px solid white;
}

.modal_title {
    text-align: center;
    color: aliceblue;
    font-size: 3rem;
}

img {
    width: 17rem;
    position: relative;
    left: 7%;
    top: 0%;
}

p {
    color: #888a8d;
    font-size: 0.5rem;
    text-align: center;
    position: relative;
    top: 20%;
}

.modal_close {
    background-image: url(../lib/icon_close.png);
    background-size: 100%;
    background-repeat: no-repeat;
    position: relative;
    left: 95%;
    top: -4%;
    width: 2rem;
    height: 2rem;
}