.outContainer {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
}

.modal {
    width: 20rem;
    height: 25rem;
    background-color: rgba(0, 0, 0, 0.0);
    position: absolute;
    left: 50%;
    top: 35%;
    transform: translate(-50%, -50%);
    z-index: 100;
    border: 1px solid white;
}

.outDiv {
    width: 20rem;
    height: 21rem;
}

.img1 {
    border: none;
    background-image: url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTUkXgvOTsFoVwXkbeRBzrN1mklImQ3uEA_3g&usqp=CAU);
    background-size: cover;
    background-repeat: no-repeat;
    width: 18rem;
    height: 12rem;
    left: 0%;
    margin: auto;
}

