.modal_title {
    text-align: center;
    color: aliceblue;
    font-size: 3rem;
}

.modal_close {
    background-image: url(../lib/icon_close.png);
    background-size: 100%;
    background-repeat: no-repeat;
    position: relative;
    left: 95%;
    top: -4%;
    width: 2rem;
    height: 2rem;
}